.data-page {
  width: 100%;
  height: 100%;
  background-color: #F9F9F9;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.data-page .ant-table-cell {
  white-space: nowrap !important;
}
.data-page .left {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0px 17px;
  padding: 17px 0px 0 0;
  box-sizing: border-box;
  overflow-y: hidden;
  position: relative;
  background-color: white;
  height: 100%;
}
.data-page .table-box {
  width: 100%;
  -webkit-flex: 1 1;
          flex: 1 1;
  overflow-x: scroll;
  overflow-y: hidden;
}
.data-page .right {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 361px;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0px 0px 10px 10px rgba(167, 167, 167, 0.5);
  z-index: 99;
  background-color: white;
}
